import React from 'react'

function geoFindMe() {
  alert('locating')
  var output = document.getElementById('map')

  if (!navigator.geolocation) {
    output.innerHTML = '<p>Geolocation is not supported by your browser</p>'
    return
  }

  function success(position) {
    var latitude = position.coords.latitude
    var longitude = position.coords.longitude

    output.appendChild(document.createElement('p')).innerHTML =
      '<p>Latitude is ' + latitude + '° <br>Longitude is ' + longitude + '°</p>'

    // var img = new Image()
    // img.src =
    //   'https://maps.googleapis.com/maps/api/staticmap?center=' +
    //   latitude +
    //   ',' +
    //   longitude +
    //   '&zoom=13&size=300x300&sensor=false'

    //output.appendChild(img)
  }

  function error() {
    output.innerHTML = 'Unable to retrieve your location'
  }

  output.appendChild(document.createElement('p')).innerHTML = 'Locating'

  navigator.geolocation.getCurrentPosition(success, error)
}

export default function testLocation() {
  return (
    <React-fragment>
      <div>
        <button onClick={geoFindMe}>Show my location</button>
      </div>
      <div id="map" />
    </React-fragment>
  )
}
